@use '../utilities/' as *;


.card {
    width: 100%;
    padding: rem(32);
    border-radius: rem(5);
    overflow: hidden;
    line-height: rem(23);
    background-color: var(--clr-white);
    box-shadow:  4px 8px 10px 0 rgb(0 0 0 / 3%), -4px 8px 10px 0 rgb(0 0 0 / 3%);
    @include breakpoint('medium') {
        width: rem(350);
    }
    &__title {
        margin-bottom: rem(16);
    }
    &__description {
        font-size: rem(13);
    }
    &__logo {
        margin-top: rem(40);
        display: block;
        margin-left: auto;
    }

    &--supervisor {
        border-top: 3px solid var(--clr-cyan);
        @include breakpoint('large'){
            grid-column: 1 / 2;
            grid-row: 2 / 4;
        }
    }
    &--builder {
        border-top: 3px solid var(--clr-red);
        @include breakpoint('large'){
            grid-column: 2 / 3;
            grid-row: 1 / 3;
        }
    }
    &--karma {
        border-top: 3px solid var(--clr-orange);
        @include breakpoint('large'){
            grid-column: 2 / 3;
            grid-row: 3 / 5;
        }
    }
    &--calculator {
        border-top: 3px solid var(--clr-blue);
        @include breakpoint('large'){
            grid-column: 3 / 4;
            grid-row: 2 / 4;
        }
    }
}